<template>
    <div class="mypanel">

        <div class="hd">
           系统日志管理
        </div>

        <div class="bd">

        <div class="action-details">
            <el-button-group>
                <!--{{export-view}}-->
                <el-button type="danger" icon="el-icon-delete" @click="deleteRec">删除</el-button>

            </el-button-group>
        </div>

        <table id="syslog" width="100%" border="0" cellspacing="0" cellpadding="0" class="ui-record-table">
            <thead>
            <tr>
                <th>
                    <input type="checkbox" @click="selectedAllHandler" v-model="selectedAll"/>
                </th>


                <th><b>标题</b></th>

                <th><b>时间</b></th>


                <th><b>机器</b></th>



                <th>
                    操作
                </th>

            </tr>

            </thead>
            <tbody>


            <tr v-for="(syslog) in  listSyslog" :key="syslog.id">
                <td>
                    <input class="check" name="ids" type="checkbox" v-model="syslog.rowSelected"/>


                </td>


                <td>
                    {{syslog.title}}
                </td>

                <td>
                    {{syslog.createtime}}
                </td>


                <td>
                    {{syslog.ip}}
                </td>




                <td>
                    <el-button-group>

                        <el-button type="warning" @click="showInfodlg(syslog)"><i class="el-icon-info"></i> 详细</el-button>
                    </el-button-group>
                </td>
            </tr>

            <tr v-if="listSyslog.length==0">
                <td colspan="20">
                    没有相关系统日志信息
                </td>
            </tr>


            </tbody>
        </table>
        <el-pagination
            @size-change="pagesizeChange"
            @current-change="pageindexChange"
            :current-page="pageindex"
            :page-sizes="[pagesize]"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        <div class="clear"></div>


        <el-dialog :visible.sync="infodlg" title="系统日志详细">

            <table class="smart-table" border="1" cellspacing="1" width="100%">



                <tr>
                    <td class="tlabel" align="right">标题:</td>
                    <td>
                        <div v-html="syslog.title"></div>
                    </td>
                </tr>

                <tr>
                    <td class="tlabel" align="right">时间:</td>
                    <td>
                        {{syslog.createtime}}
                    </td>
                </tr>


                <tr>
                    <td class="tlabel" width="10%" align="right">机器:</td>
                    <td width="*">
                        {{syslog.ip}}
                    </td>
                </tr>




            </table>


        </el-dialog>

        </div>
    </div>
</template>

<script>
    import {VueUtil} from "../util/vueutil";

    export default {
        name: "syslogmanager",
        data() {
            return {
                "title": "",
                hostHead: this.$app.contextPath,
                selectedAll: false,
                listSyslog: [],
                pageindex: 1, //初始页
                pagesize: 10,
                total: 10,
                infodlg: false,
                syslog: {}
            }

        },
        methods: {
            pagesizeChange: function (pagesize) {
                this.pagesize = pagesize;
            },
            pageindexChange: function (pageindex) {
                this.pageindex = pageindex;
                console.log(this.pageindex);
                this.search();
            },
            async search() {
                let url = "admin/syslog/list";
                let param = {
                    currentpageindex: this.pageindex,
                    pagesize: this.pagesize,
                    "title": this.title,
                };
                let util = new VueUtil(this);
                console.log("this.pageindex=" + this.pageindex);
                let res = await util.http.post(url, param);
                if (res.data != null) {
                    let pageInfo = res.data.data;
                    this.total = pageInfo.total;
                    this.listSyslog = pageInfo.list;
                    console.log(pageInfo);
                }
            },
            async deleteRec() {
                let url = "admin/syslog/delete";
                let util = new VueUtil(this);
                let hasChecked = this.listSyslog.some(c => {
                    return c.rowSelected == true;
                });
                if (!hasChecked) {
                    util.alert('请选择需要删除的记录', '系统提示', {
                        confirmButtonText: '确定'
                    });
                    return;
                }

                let ids = this.listSyslog.filter(c => c.rowSelected).map(c => c.id);
                let res = util.confirm('是否要删除数据?', '系统提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(() => {
                    util.http.post(url, {ids}, {emulateJSON: false}).then(res => {
                        if (res.data != null && res.data.stateCode > 0) {
                            this.search();
                        }
                    });
                }).catch(() => {
                });
            },
            exportHandler() {
                window.location.href = this.hostHead + "/admin/syslog/expor";
            },
            selectedAllHandler() {
                console.log(this.selectedAll);
                if (this.listSyslog != null) {
                    this.listSyslog.forEach(c => {
                        c.rowSelected = !this.selectedAll;
                    });
                }
            },
            showInfodlg(syslog) {
                this.syslog = syslog;
                this.infodlg = true;
            },
        },
        created() {
            this.search();
        }
    }
</script>

<style scoped>

</style>
